<template>
    <v-snackbar
            top
            v-model="snackbar"
            :color="color"
            :timeout="timeout"
    >
        {{ message }}
    </v-snackbar>
</template>

<script>
    export default {
        name: 'Snackbar',
        data () {
            return {
                snackbar: false,
                message: '',
                color: '',
                timeout: 3000,
            }
        },
        methods: {
            show (color, message, cb) {
                this.message = message;
                this.color = color;
                this.timeout = 3000;
                this.snackbar = true;
                if (cb && typeof cb === 'function')
                    setTimeout(() => cb(), this.timeout);
            },
        }
    }
</script>

